'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";
import MultiSelect from "@/app/_components/multiselect";
import SelectEntry from "@/app/_components/selectentry";
import FormDescription from "@/app/_components/formdescription";

let pageDescription = ['Update an Existing Session']

let submitObject = {};

const initState = {
    'Select Session':{
        options: [{ID:0, Name:''}],
        value:0
    },
    'Trainer':{
        options: [{ID:0,Name:''}],
        value:0
    },
    'Remote Trainer':{
        options: [],
        value:0
    },
    'Room':{
        options: [],
        value:236
    },
    'Shadow Name': {
        value: '',
    },
    'Teams Link': {
        value: '',
    },
    'Session Date': {
        value: ''
    },
    'Session Length': {
        value: 60
    },
    'Select Students':{
        options: [],
        value:[]
    },
    'Has Shadow':{
        isChecked: false
    },
    'Needs Reserved':{
        isChecked: false
    },
    'Has Remote':{
        isChecked: false
    },
    'Assets List':[],
    'Sessions List':[],
    'Trainers List':[],
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};

const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            console.dir(action);

            newState[action.label].value = action.value;

            if(action.label=='Select Course'){
                
                let course = action.value;


                try{
                    newState['Select Students'].options = state['Session List'].find(session=>session.ID == course).Learners;
                }catch{
                    newState['Select Students'].options = [];
                }

                

            }

            return newState

            break
        }

        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }

        case 'load-SelectBox':{

            let Sessions = action.data.Sessions;

            newState['Select Session'].options = Sessions;

            newState['Select Session'].value = Sessions[0].ID;

            return newState

            break

        }

        case 'load-Response':{

            let response = action.data.data;

            newState['Session Date'].value = response.Ticket.DueDate;

            newState['Room'].options = response.Rooms;
            newState['Room'].value = response.Ticket.UserDefinedValues.find(u=>u.UserDefinedFieldID === 214).Value;

            newState['Trainer'].options = response.Trainers;
            newState['Trainer'].value = response.Ticket.AssignedToUserID;

            //newState['Remote Trainer'].options = response.Ticket.UserDefinedValues.find(u=>u.UserDefinedFieldID === 314).Value;
            newState['Remote Trainer'].value = response.Ticket.UserDefinedValues.find(u=>u.UserDefinedFieldID === 314).Value;

            newState['Shadow Name'].value = response.Ticket.UserDefinedValues.find(u=>u.UserDefinedFieldID === 312).Value;

            newState['Teams Link'].value = response.Ticket.UserDefinedValues.find(u=>u.UserDefinedFieldID === 308).Value;

            return newState

            break;

        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }

        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

    }
};

export default function UpdateSession() {

    const [state, dispatch] = useReducer(reducer,initState);



    useEffect(function(){

                const loadDropListOptions = async()=>{

                    let options = await fetch({method:'GET',endpoint:'Litmos/RetrieveUpcomingSessions'});

                    if(options.didError){

                        //Do stuff

                        return

                    }

                    dispatch({type:'load-SelectBox',data:options.data})

                }

                loadDropListOptions();
            
                return

            },[]
    );

    submitObject = {
        MasterTicket: state['Select Session'].value,
        Room: state['Room'].value,
        Shadow: state['Shadow Name'].value,
        Trainer: state['Trainer'].value,
        Remote: state['Remote Trainer'].value,
        Link: state['Teams Link'].value,
        DueDate: state['Session Date'].value}

  return (
    <main className={styles.main} style={{ backgroundImage: `url(${backgroundImage.src})` }}>
        <Form>
            <FormTitle text='Update Existing Session'></FormTitle>
            <FormDescription text={pageDescription}></FormDescription>

            <SelectEntry
                label='Select Session'
                value={state['Select Session'].value}
                dispatch={dispatch}
                endpoint={'Internal/LoadLiveTicketAndOptions'}
                data={{ Ticket: state['Select Session'].value }}
                options={state['Select Session'].options}>
            </SelectEntry>

            <Select
                options={state['Trainer'].options}
                label='Trainer'
                value={state['Trainer'].value}
                dispatch={dispatch}
            ></Select>

            <EntryBox
                label='Shadow Name'
                type = 'text'
                reminder =''
                placeholder = ''
                value = {state['Shadow Name'].value}
                dispatch={dispatch}
                hidden={false}
            ></EntryBox>

            <EntryBox
                label='Remote Trainer'
                type = 'text'
                reminder =''
                placeholder = ''
                value = {state['Remote Trainer'].value}
                dispatch={dispatch}
                hidden={false}
            ></EntryBox>

            <EntryBox
                label='Session Date'
                type = 'datetime-local'
                reminder =''
                placeholder = ''
                value = {state['Session Date'].value}
                dispatch={dispatch}
            ></EntryBox>
            
            <Select
                options={state['Room'].options}
                label='Room'
                value={state['Room'].value}
                dispatch={dispatch}
            ></Select>

            <EntryBox
                label='Teams Link'
                type = 'text'
                reminder =''
                placeholder = ''
                value = {state['Teams Link'].value}
                dispatch={dispatch}
            ></EntryBox>

            <Submit
                method="POST"
                endpoint="Internal/UpdateExistingSession"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>

            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
        </Form>
    </main>
  );
}
